import './Login.css';
import { Button, Input, notification, NotificationArgsProps, ConfigProvider } from 'antd';
import { EyeInvisibleOutlined, EyeTwoTone, UserOutlined, LockOutlined } from '@ant-design/icons';

//notification type
type NotificationPlacement = NotificationArgsProps['placement'];

const Login = () => {

    // Notification for incorrect username or password
    const [api, contextHolder] = notification.useNotification();
    const openNotification = (placement: NotificationPlacement) => {
        api.error({
          message: 'Incorrect',
          description:
            'Username or Password is incorrect. Please try again.',
          placement
        });
      };

    //Display part
    return (
        <div>
            {contextHolder}
            <h1 className='title'>ກະລຸນາເຂົ້າຜ່ານຄອມພິວເຕີ</h1>
            <div className='screen'>
                <div className='frame' />
                <div className='logo' />
                <Input className='txtUsr' placeholder='Username' prefix={<UserOutlined/>}/>
                <Input.Password className='txtPwd' placeholder='Password' iconRender={(visible) => (visible? <EyeTwoTone/> : <EyeInvisibleOutlined/>)} prefix={<LockOutlined/>}/>
                <ConfigProvider theme={{ token: { colorPrimary: '#142E3F' } }}>
                  <Button className='btnLogin' type='primary' onClick={() => openNotification('bottomRight')}>Login</Button>
                </ConfigProvider>
            </div>
        </div>
    );
};

export default Login;