import './Staff.css';
import { useState, useEffect } from 'react';
import Nav from '../navPage/Nav';
import { Input, ConfigProvider, Button, FloatButton, Avatar, List, Skeleton, Drawer, Modal } from 'antd';
import { UserAddOutlined, EditTwoTone, DeleteTwoTone, UserOutlined, LockOutlined, EyeTwoTone, EyeInvisibleOutlined, MobileOutlined } from '@ant-design/icons'

const { Search } = Input;

interface staffData {
    gender?: string;
    name?: string;
    email?: string;
    avatar?: string;
    loading: boolean;
  }

const PAGE_SIZE = 10;

//List of staff data
const Staff = () => {

    //Search function
    const onSearch = (value: string) => {};

    //Load staff data on startup
    const [initLoading, setInitLoading] = useState(true);
    const [loading, setLoading] = useState(false);
    const [data, setData] = useState<staffData[]>([]);
    const [list, setList] = useState<staffData[]>([]);
    const [page, setPage] = useState(1);
  
    const fetchData = (currentPage: number) => {
      const fakeDataUrl = `https://660d2bd96ddfa2943b33731c.mockapi.io/api/users?page=${currentPage}&limit=${PAGE_SIZE}`;
      return fetch(fakeDataUrl).then((res) => res.json());
    };
  
    useEffect(() => {
      fetchData(page).then((res) => {
        const results = Array.isArray(res) ? res : [];
        setInitLoading(false);
        setData(results);
        setList(results);
      });
    }, []);
  
    //Load more data when clicking the button
    const onLoadMore = () => {
      setLoading(true);
      setList(data.concat(Array.from({ length: PAGE_SIZE }).map(() => ({ loading: true }))));
      const nextPage = page + 1;
      setPage(nextPage);
      fetchData(nextPage).then((res) => {
        const results = Array.isArray(res) ? res : [];
        const newData = data.concat(results);
        setData(newData);
        setList(newData);
        setLoading(false);
        window.dispatchEvent(new Event('resize'));
      });
    };
  
    //Load more button
    const loadMore = !initLoading && !loading ? (
        <div
          style={{
            textAlign: 'center',
            marginTop: 12,
            height: 32,
            lineHeight: '32px',
          }}
        >
          <Button onClick={onLoadMore} style={{width: 100}}>ເບິ່ງເພີ່ມ</Button>
        </div>
    ) : null;

    //Delete staff
    const [isModalOpen, setIsModalOpen] = useState(false);

    const showModal = () => {
      setIsModalOpen(true);
    };

    const handleOk = () => {
      //delete staff at here
      setIsModalOpen(false);
    };

    const handleCancel = () => {
      setIsModalOpen(false);
    };

    //Show Staff details
    const [staffName, setStaffName] = useState('');
    const [staffPic, setStaffPic] = useState('');
    const [open, setOpen] = useState(false);
    const handleEdit = (fullName: string, pic: string) => {
      setStaffName(fullName);
      setStaffPic(pic);
      setOpen(true);
    };

    const closeDetails = () => {
      setOpen(false);
    };

    return (
        <div>
            <Nav />
            <ConfigProvider theme={{ token: { colorPrimary: '#142E3F' } }}>
                <Search className='txtNameID' placeholder='ຄົ້ນຫາດ້ວຍຊື່ ຫລື ລະຫັດພະນັກງານ' onSearch={onSearch} enterButton/>
                <FloatButton
                    shape="square"
                    type="primary"
                    style={{
                        position: 'absolute',
                        left: 'calc(100% - 75px)',
                        top: 'calc(100% - 75px)',
                        backgroundColor: '#142E3F',
                        insetInlineEnd: 24 
                    }}
                    icon={<UserAddOutlined />}
                />
            </ConfigProvider>
            <Modal title='ຕ້ອງການລົບແທ້ບໍ?' open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
              <p>ຕ້ອງການລົບແທ້ບໍ? ( ຫລັງຈາກລົບແລ້ວຈະບໍ່ສາມາດກູ້ຄືນໄດ້ )</p>
            </Modal>
            <div
                id='scrollableDiv'
                style={{
                    display: 'absolute',
                    position: 'absolute',
                    left: 260,
                    top: 60,
                    width: 'calc(100% - 310px)',
                    height: 'calc(100% - 80px)',
                    overflow: 'auto',
                    padding: '0 16px',
                    borderRadius: '4px',
                    border: '1px solid rgba(140, 140, 140, 0.35)',
                }}
            >
                <List
                  loading={initLoading}
                  itemLayout="horizontal"
                  loadMore={loadMore}
                  dataSource={list}
                  renderItem={(item) => (
                    <List.Item
                       actions={[<EditTwoTone onClick={()=>{handleEdit(String(item.name), String(item.avatar))}} twoToneColor={'#142E3F'}/>, <DeleteTwoTone onClick={showModal} twoToneColor={'#E74C3C'}/>]}
                    >
                      <Skeleton avatar title={false} loading={item.loading} active>
                        <List.Item.Meta
                          avatar={<Avatar src={item.avatar} />}
                          title={<a>{item.name}</a>}
                          description={item.email}
                        />
                      </Skeleton>
                    </List.Item>
                  )}  
                />
            </div>
            <Drawer title='STF-0001' onClose={closeDetails} open={open} width={400}>
              <Avatar shape='square' size={220} src={staffPic} style={{left: 66, top: 8}}/>
              <Input style={{left: 42, top: 44, width: 280}} placeholder='ຊື່' defaultValue={staffName} prefix={<UserOutlined/>}/>
              <Input.Password style={{left: 42, top: 54, width: 280}} placeholder='ລະຫັດຜ່ານ' iconRender={(visible) => (visible? <EyeTwoTone/> : <EyeInvisibleOutlined/>)} prefix={<LockOutlined/>}/>
              <Input type='number' style={{left: 42, top: 64, width: 280}} placeholder='ເບີໂທ' prefix={<div><MobileOutlined/><a>+856</a></div>} suffix={<></>}/>
              <Input.TextArea style={{left: 42, top: 74, width: 280, height: 120}} placeholder='ທີ່ຢູ່'/>
              <ConfigProvider theme={{ token: { colorPrimary: '#142E3F' } }}>
                <Button style={{left: 94, top: 90, width: 180}} type='primary'>ບັນທຶກ</Button>
              </ConfigProvider>
            </Drawer>
        </div>
    );
};

export default Staff;