import './Nav.css';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router';
import { Menu, MenuProps } from 'antd';
import { FundOutlined, TeamOutlined, ProductOutlined, CodeSandboxOutlined, ShoppingOutlined, FileTextOutlined, TruckOutlined, ShoppingCartOutlined, ShopOutlined, GoldOutlined } from '@ant-design/icons';

const Nav = () => {
    //Navigate

    const navigate = useNavigate();

    //Menu items
    type MenuItem = Required<MenuProps>['items'][number];
    const items: MenuItem[] = [
        {
            key: 'btn1',
            label: 'ຈັດການພະນັກງານ',
            icon: <TeamOutlined />
        },
        {
            key: 'btn2',
            label: 'ຈັດການປະເພດສິນຄ້າ',
            icon: <ProductOutlined />
        },
        {
            key: 'btn3',
            label: 'ຫົວຫນ່ວຍນັບສິນຄ້າ',
            icon: <CodeSandboxOutlined />
        },
        {
            key: 'btn4',
            label: 'ສິນຄ້າ',
            icon: <ShoppingOutlined />
        },
        {
            key: 'btn5',
            label: 'ສັງຊື້ສິນຄ້າ',
            icon: <FileTextOutlined />
        },
        {
            key: 'btn6',
            label: 'ນຳເຂົ້າສິນຄ້າດ້ວຍໃບສັ່ງ',
            icon: <TruckOutlined />
        },
        {
            key: 'btn7',
            label: 'ນຳເຂົ້າສິນຄ້າໂດຍກົງ',
            icon: <ShoppingCartOutlined />
        },
        {
            key: 'btn8',
            label: 'ລາຍງານສາງສິນຄ້າ',
            icon: <GoldOutlined />
          },
        {
          key: 'btn9',
          label: 'ລາຍງານຍອດຂາຍ',
          icon: <FundOutlined />
        },
        {
          key: 'btn10',
          label: 'ຈັດການຂໍ້ມູນຮ້ານ',
          icon: <ShopOutlined />
        }
    ];

    //On Startup
    const [menuKey, setMenuKey] = useState<string>('');
    useEffect(() => {
        setMenuKey(localStorage.getItem('menuKey') || '');
    });

    //On Close
    const UnloadAdminButton = () => {
        window.onunload = () => {
            localStorage.removeItem('menuKey');
        }
    }
    UnloadAdminButton();

    //Menu click event
    const onClick: MenuProps['onClick'] = (e) => {
        if(e.key === 'btn1') {
            navigate('/admin/staff');
        }

        localStorage.setItem('menuKey', e.key);
        setMenuKey(e.key);
    };

    return (
        <div>
            <Menu className='menu'
                onClick={onClick}
                style={{ width: 220 }}
                selectedKeys={[menuKey]}
                mode="inline"
                items={items}
            />
        </div>
    );  
};

export default Nav;